<template>
    <div class="demo-checkbox">
        <demo-row title="基本用法">
            <pl-checkbox label="标签一" v-model="val[0]"/>
            <pl-checkbox label="标签二" v-model="val[0]"/>
            {{val[0]}}
        </demo-row>

        <demo-row title="状态">
            <pl-checkbox v-for="item in status" :label="item" :status="item" :key="item" :value="true"/>
        </demo-row>

        <demo-row title="禁用">
            <pl-checkbox :value="true" :disabled="val[1]" label="标签一"/>
            <pl-checkbox :value="false" :disabled="val[1]" label="标签二"/>
            <pl-checkbox v-model="val[1]" label="禁用"/>
        </demo-row>

        <demo-row title="真假值">
            <pl-checkbox label="标签一" v-model="val[2]" :trueValue="10" :falseValue="20"/>
            {{val[2]}}
        </demo-row>

        <demo-row title="大小">
            <pl-checkbox label="mini" size="mini"/>
            <pl-checkbox label="normal" size="normal"/>
            <pl-checkbox label="large" size="large"/>
            <pl-checkbox label="font-size:24px" style="font-size: 24px"/>
        </demo-row>

        <demo-row title="checkbox-inner">
            <div style="font-size: 12px" class="demo-checkbox-row">
                <pl-checkbox-inner :disabled="disabledFlag"/>
                <pl-checkbox-inner status="uncheck" :disabled="disabledFlag"/>
                <pl-checkbox-inner status="minus" :disabled="disabledFlag"/>
            </div>
            <div style="font-size: 14px" class="demo-checkbox-row">
                <pl-checkbox-inner :disabled="disabledFlag"/>
                <pl-checkbox-inner status="uncheck" :disabled="disabledFlag"/>
                <pl-checkbox-inner status="minus" :disabled="disabledFlag"/>
            </div>
            <div style="font-size: 16px" class="demo-checkbox-row">
                <pl-checkbox-inner :disabled="disabledFlag"/>
                <pl-checkbox-inner status="uncheck" :disabled="disabledFlag"/>
                <pl-checkbox-inner status="minus" :disabled="disabledFlag"/>
            </div>
        </demo-row>

        <demo-row title="复选框组">
            <pl-checkbox-group v-model="val[3]">
                <pl-checkbox label="标签一" val="tag1"/>
                <pl-checkbox label="标签二" val="tag2"/>
                <pl-checkbox label="标签三" val="tag3"/>
                {{val[3]}}
            </pl-checkbox-group>
        </demo-row>

        <demo-row title="自定义渲染内容">
            <pl-checkbox-group v-model="val[3]">
                <pl-checkbox v-for="item in ['tag1','tag2','tag3']" :val="item" :key="item">
                    <div slot-scope="{checked}" class="demo-checkbox-custom-item" :class="{'demo-checkbox-custom-item-active':checked}">
                        {{item}}
                    </div>
                </pl-checkbox>
            </pl-checkbox-group>
        </demo-row>

        <demo-row title="复选框组：状态以及大小">
            <pl-checkbox-group v-model="val[3]" status="warn" size="large">
                <pl-checkbox label="标签一" val="tag1"/>
                <pl-checkbox label="标签二" val="tag2"/>
                <pl-checkbox label="标签三" val="tag3"/>
                {{val[3]}}
            </pl-checkbox-group>
        </demo-row>

        <demo-row title="复选框组：禁用与只读">
            <pl-checkbox-group v-model="val[3]" disabled>
                <pl-checkbox label="标签一" val="tag1"/>
                <pl-checkbox label="标签二" val="tag2"/>
                <pl-checkbox label="标签三" val="tag3"/>
            </pl-checkbox-group>
            <pl-checkbox-group v-model="val[3]" readonly>
                <pl-checkbox label="标签一" val="tag1"/>
                <pl-checkbox label="标签二" val="tag2"/>
                <pl-checkbox label="标签三" val="tag3"/>
            </pl-checkbox-group>
            {{val[3]}}
        </demo-row>

        <demo-row title="复选框组：最大最小勾选个数(全选会勾选最大可勾选个数)">
            <pl-checkbox-group v-model="val[4]" :max="3" :min="1">
                <pl-checkbox label="标签一" val="tag1"/>
                <pl-checkbox label="标签二" val="tag2"/>
                <pl-checkbox label="标签三" val="tag3"/>
                <pl-checkbox label="标签四" val="tag4"/>
                <pl-checkbox label="标签五" val="tag5"/>
                {{val[4]}}
            </pl-checkbox-group>
        </demo-row>


    </div>
</template>

<script>
    import DemoMixins from "../../component/DemoMixins";

    export default {
        name: "demo-checkbox",
        mixins: [DemoMixins],
        props: {},
        data() {
            return {
                disabledFlag: false,
                val: {
                    1: true
                },
            }
        },
        methods: {},
    }
</script>

<style lang="scss">
    .demo-checkbox {
        .demo-checkbox-row {
            & > * {
                margin-right: 1em;
            }
        }

        .demo-checkbox-custom-item {
            height: 80px;
            width: 80px;
            border-radius: 4px;
            border: solid 1px #f1f1f1;
            display: flex;
            align-items: center;
            justify-content: center;

            &.demo-checkbox-custom-item-active {
                background-color: #f1f1f1;
            }
        }
    }
</style>